//
// Fonts
// --------------------------------------------------

// Import the fonts
@font-face {
    font-family: 'Open Sans Condensed';
    src: url('@{icon-font-path}OpenSans-CondBold-webfont.eot');
    src: url('@{icon-font-path}OpenSans-CondBold-webfont.eot?#iefix') format('embedded-opentype'),
         url('@{icon-font-path}OpenSans-CondBold-webfont.woff') format('woff'),
         url('@{icon-font-path}OpenSans-CondBold-webfont.ttf') format('truetype'),
         url('@{icon-font-path}OpenSans-CondBold-webfont.svg#open_sans_condensedbold') format('svg');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'Open Sans Condensed';
    src: url('@{icon-font-path}OpenSans-CondLight-webfont.eot');
    src: url('@{icon-font-path}OpenSans-CondLight-webfont.eot?#iefix') format('embedded-opentype'),
         url('@{icon-font-path}OpenSans-CondLight-webfont.woff') format('woff'),
         url('@{icon-font-path}OpenSans-CondLight-webfont.ttf') format('truetype'),
         url('@{icon-font-path}OpenSans-CondLight-webfont.svg#open_sans_condensed') format('svg');
    font-weight: normal;
    font-style: normal;
}

@font-face {
  font-family: 'League Gothic';
    src: url('@{icon-font-path}leaguegothic-regular-webfont.eot');
    src: url('@{icon-font-path}leaguegothic-regular-webfont.eot?#iefix') format('embedded-opentype'),
         url('@{icon-font-path}leaguegothic-regular-webfont.woff') format('woff'),
         url('@{icon-font-path}leaguegothic-regular-webfont.ttf') format('truetype'),
         url('@{icon-font-path}leaguegothic-regular-webfont.svg#league_gothic') format('svg');
    font-weight: normal;
    font-style: normal;
}