// Tiles
// --------------------------------------------------

.tile {
    -webkit-perspective: 0;
    -webkit-transform-style: preserve-3d;
    -webkit-transition: -webkit-transform .2s;
    float: left;
    opacity: 0.75;
    border: 0;
    width: 100%;
    /*margin-bottom: 29px;*/
    position: relative;
    -webkit-transition: opacity .2s linear;
       -moz-transition: opacity .2s linear;
         -o-transition: opacity .2s linear;
            transition: opacity .2s linear;
}
.tile-wrapper {
	padding: 10px;
}
.tile-content
{
    padding: 5px 10px 10px 10px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    color: White;
}
.dummy {
    margin-top: 100%;
}

.tile-content a
{
    color: White;
    text-decoration: underline;   
}
.tile-content p 
{
    margin-top: 15px;
}
.tile-content h1,
.tile-content h2,
.tile-content h3,
.tile-content h4,
.tile-content h5,
.tile-content h6 {
    color: #fff;
    -webkit-user-select: none;
    margin: 0;
}
.tile-content img { border: 0 }
.tile:hover { opacity: 1 }