// Accordion
// --------------------------------------------------

#accordion {
	width: 100%;
	background-color: @cort-white;
	margin-bottom: 20px;
	
	.accordionButton {
		background-color: @cort-red-charcoal;
		padding: 10px;
		color: @cort-white;
		font-size: 20px;
		margin-bottom: 2px;
		cursor: hand;
		cursor: pointer;
	}
	
	.accordionContent {
		display: none;
		padding: 10px;
	}
}

#tinymce {
	.accordionContent {
		display: block;
	}
}

.cort-accordion {
	height: auto;
	overflow: hidden;
	background-color: @cort-white;
	margin-top: @grid-gutter-width;
	margin-bottom: @grid-gutter-width;
	position: relative;
	
	h3 {
		background-color: @cort-gray-warmer;
		margin: 0;
		color: @gray-darker;
		width: 100%;
		line-height: 40px;
		border-bottom: 2px solid @cort-gray-warm;
		border-right: 2px solid @cort-gray-warm;
		padding-left: 15px;
		cursor: hand;
		cursor: pointer;
		font-family: @font-family-base;
		font-size: 22px;
		font-weight: normal;
		
		&.last {
			border-bottom: none;
		}
		
		&:hover {
			#gradient > .vertical(@cort-gray-warmer, @cort-gray-warm, 0%, 100%);
		}
		
		&.active {
			background-color: @cort-red;
			color: @cort-white;
		}
		
		&.active:hover {
			#gradient > .vertical (@cort-red, @cort-red-dark, 0%, 100%);
		}
	}
	
	& > .active {
		display: block !important;
	}
	
	.cort-accordion-content {
		background-color: @cort-white;
		display: none;
		position: relative;
		width: 100%;
		
		.cort-accordion-content-inner {
			position: absolute;
			top: 0;
			width: 100%;
			padding-left: (@grid-gutter-width / 2);
			padding-right: (@grid-gutter-width / 2);

			h1 {
				color: @cort-white;
			}
			
			.description {
				margin-top: 20px;
				width: 50%;
				float: right;
				color: @cort-white;
				
				ul {
					list-style: none;
					
					li {
						list-style: none;
						font-weight: normal;
						line-height: 30px;
						text-shadow: 2px 2px 2px #333;
						
						a {
							color: @cort-white;
							font-size: 18px;
							
							&:hover {
								color: @cort-white;
							}
						}
					}
				}
			}
			
			@media (max-width: (@screen-lg-min)) {
				.description {
					ul {
						li {
							line-height: 27px;
							a {
								font-size: 16px;
							}
						}
					}
				}
			}
			
			@media (max-width: (@screen-md-min)) {
				.description {
					position: relative;
					width: 100%;
					float: none;
					color: @cort-black;
					
					ul {
						list-style: none;
						
						li {
							list-style: none;
							line-height: 30px;
							text-shadow: none;
							
							a {
								color: @cort-black;
								font-size: 16px;
								
								&:hover {
									color: @cort-black;
								}
							}
						}
					}
				}
			}
			
			@media (max-width: (@screen-sm-min)) {
				.description {
					position: relative;
					width: 100%;
					float: none;
					color: @cort-black;
				}
			}
			
			@media (max-width: (@screen-xs-min)) {
				.description {
					position: relative;
					width: 100%;
					float: none;
					color: @cort-black;
				}
			}
		}
		
		@media (max-width: (@screen-md-min)) {
			.cort-accordion-content-inner {
				position: relative;
			}
		}
		
		@media (max-width: (@screen-sm-min)) {
			.cort-accordion-content-inner {
				position: relative;
			}
		}
		
		@media (max-width: (@screen-xs-min)) {
			.cort-accordion-content-inner {
				position: relative;
			}
		}
	}
	
	@media (min-width: @screen-sm-min) {
		.cort-accordion-content {
			width: 100%;
		}
	
		h3 {
			width: 100%;
			line-height: 40px;
			padding-left: 15px;
		}
	}
	@media (min-width: @screen-md-min) {
		.cort-accordion-content {
			position: absolute;
			top: 0;
			right: 0;
			width: 68%;
		}
	
		h3 {
			width: 32%;
			float: left;
			line-height: 48px;
			padding-left: 30px;
			clear: left;
		}
	}
	@media (min-width: @screen-lg-min) {
		h3 {
			line-height: 58px;
			padding-left: 30px;
		}
	}
	
	
}