//
// Scaffolding
// --------------------------------------------------


// Reset the box-sizing
//
// Heads up! This reset may cause conflicts with some third-party widgets.
// For recommendations on resolving such conflicts, see
// http://getbootstrap.com/getting-started/#third-box-sizing
* {
  .box-sizing(border-box);
}
*:before,
*:after {
  .box-sizing(border-box);
}


// Body reset

html {
  font-size: 10px;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  overflow-x: hidden;
}

body {
  font-family: @font-family-base;
  font-size: @font-size-base;
  font-weight: 300;
  line-height: @line-height-base;
  color: @text-color;
  background-color: @body-bg;
  overflow-x: hidden;
}

// Reset fonts for relevant elements
input,
button,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}


// Links

a {
  color: @link-color;
  text-decoration: none;

  &:hover,
  &:focus {
    color: @link-hover-color;
    text-decoration: @link-hover-decoration;
  }

  &:focus {
    .tab-focus();
  }
}


// Figures
//
// We reset this here because previously Normalize had no `figure` margins. This
// ensures we don't break anyone's use of the element.

figure {
  margin: 0;
}


// Images

img {
  vertical-align: middle;
}

// Responsive images (ensure images don't scale beyond their parents)
.img-responsive {
  .img-responsive();
}

// Rounded corners
.img-rounded {
  border-radius: @border-radius-large;
}

// Image thumbnails
//
// Heads up! This is mixin-ed into thumbnails.less for `.thumbnail`.
.img-thumbnail {
  padding: @thumbnail-padding;
  line-height: @line-height-base;
  background-color: @thumbnail-bg;
  border: 1px solid @thumbnail-border;
  border-radius: @thumbnail-border-radius;
  .transition(all .2s ease-in-out);

  // Keep them at most 100% wide
  .img-responsive(inline-block);
}

// Perfect circle
.img-circle {
  border-radius: 50%; // set radius in percents
}


// Horizontal rules

hr {
  margin-top:    @line-height-computed;
  margin-bottom: @line-height-computed;
  border: 0;
  border-top: 1px solid @hr-border;
}

hr.faded {
	margin-top:    @line-height-computed;
    margin-bottom: @line-height-computed;
    border: 0;
    height: 1px;
	background-image: -webkit-linear-gradient(left, rgba(102,102,102,0), rgba(102,102,102,0.75), rgba(102,102,102,0)); 
	background-image:    -moz-linear-gradient(left, rgba(102,102,102,0), rgba(102,102,102,0.75), rgba(102,102,102,0)); 
	background-image:     -ms-linear-gradient(left, rgba(102,102,102,0), rgba(102,102,102,0.75), rgba(102,102,102,0)); 
	background-image:      -o-linear-gradient(left, rgba(102,102,102,0), rgba(102,102,102,0.75), rgba(102,102,102,0)); 
}

hr.rounded-drop-shadow {
	margin: 0;
	height: 35px;
	width: 100%;
	z-index: 1;
	position: absolute;
	top: 0;
	background-color: transparent;
	background-image: url("../images/rounded-dropshadow.png");
	background-size: 100%;
	background-repeat: no-repeat;
}


// Only display content to screen readers
//
// See: http://a11yproject.com/posts/how-to-hide-content/

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0,0,0,0);
  border: 0;
}

// Use in conjunction with .sr-only to only display content when it's focused.
// Useful for "Skip to main content" links; see http://www.w3.org/TR/2013/NOTE-WCAG20-TECHS-20130905/G1
// Credit: HTML5 Boilerplate

.sr-only-focusable {
  &:active,
  &:focus {
    position: static;
    width: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    clip: auto;
  }
}
// Frames
// --------------------------------------------------

.cort-frame {
	img {
		width: 100%;
		max-width: 100%;
		display: block;
	}
	
	figcaption {
		padding: 4px;
		font-size: @font-size-small;
		font-weight: bold;
		bottom: 0;
		
		& > a {
			color: @cort-black;
			display: block;
			
			&:hover {
				text-decoration: none;
			}
		}
		
		&.cort-frame-summary {
			height: 70px;
			.text-overflow-wrap();
		}
	}
}

// Articles
// --------------------------------------------------
article {
	&.cort-news {
		//height: 300px;
		margin-bottom: (@grid-gutter-width / 2);
		
		img {
			//height: 240px;
			
			&.bordered {
				border: 4px solid @cort-white;
			}
		}
		
		@media (max-width: (@screen-lg-min)) {
			img {
				//height: 200px;
			}
		}
		
		@media (max-width: (@screen-md-min)) {
			//height: 280px;
		
			img {
				//height: 220px;
			}
		}
		
		@media (max-width: (@screen-sm-min)) {
			//height: 280px;
		
			img {
				//height: 200px;
			}
		}
		
		@media (max-width: (@screen-xs-min)) {
			//height: 200px;
		
			img {
				//height: 120px;
			}
		}
	}
	
	&.cort-event {
		margin-bottom: 5px; 
		height: 109px;
		cursor: hand;
		cursor: pointer;
		
		&:hover section.cort-event-date {
			#gradient > .vertical(@cort-red, @cort-red-dark, 0%, 100%);
		}
		
		&:hover section.cort-event-desc {
			#gradient > .vertical(@cort-gray-warm, @cort-white, 0%, 100%);
		}
		
		& section.cort-event-date {
			color: @cort-white;
			font-size: 18px;
			text-transform: uppercase;
			text-align: center;
			background-color: @cort-red;
			width: 25%;
			height: 108px;
			float: left;
			
			& .month {
				margin-top: 5px;
				font-weight: bold;
			}
			& .day {
				font-size: ceil((@font-size-large * 2.5));
				line-height: 50px;
			}
		}
		
		& section.cort-event-desc {
			background-color: @cort-gray-warm;
			color: @gray-darker;
			float: left;
			width: 75%;
			height: 108px;
			
			.cort-event-desc-inner {
				padding-left: (@grid-gutter-width / 2);
				padding-right: (@grid-gutter-width / 2);
				
				span.time {
					color: @cort-red
				}
				
				h4 {
					font-family: @font-family-base;
					font-size: @font-size-base;
					font-weight: bold;
				}
				
				p {
					line-height: @line-height-base;
					font-size: @font-size-small;
					
					span.location {
						display: block;
						white-space: nowrap;
						text-overflow: ellipsis;
						width: 100%;
						overflow: hidden;
					}
				}
			}
		}
	}
}

