// Theme utilities
// --------------------------------------------------
.floatr {
	margin-left: 10px;
	margin-bottom: 10px;
}
.floatl {
	margin-right: 10px;
	margin-bottom: 10px;
}
.floatr, .pull-right {
	float: right;

	&.img-padding {
		margin-left: 10px;
		margin-bottom: 10px;
	}
}
.floatl, .pull-left {
	float: left;

	&.img-padding {
		margin-right: 10px;
		margin-bottom: 10px;
	}	
}
.extra-gradient {
	padding: 15px;
	overflow: hidden;
	#gradient > .vertical (@cort-gray-warm, @cort-gray, 0%, 100%);
	
	.news-widget {
		padding-right: 15px;
		padding-left: 15px;
	}
	
	h2 {
		margin-top: 0;
	}
	
	@media (max-width: (@screen-md-min)) {
		h2 {
			margin-top: 22px;
		}
		padding:0;
		background-image: none;
	}
}
.field-validation-error {
    color: @cort-red;
    font-weight: bold;
}
.input-validation-error {
