}
.readMore {
	display: block;
	text-transform: uppercase;
	text-align: right;
	font-weight: normal;
	font-size: .9em;
}
.read-more-link {
	position: absolute;
	font-size: @font-size-base;
	color: @cort-red-dark;
	right: 0;
	bottom: 0;
	text-transform: uppercase;
	text-decoration: none;
	font-family: @font-family-base;
	font-weight: bold;
	
	&:hover {
		text-decoration: none;
	}
}
