//
// Navigation
// --------------------------------------------------

#navigation {
	.navbar-toggle {
		position: absolute;
		right: 0;
		padding: 9px 10px;
		margin-top: 8px;
		margin-right: 15px;
		margin-bottom: 8px;
		background-color: transparent;
		background-image: none; // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214
		border: 1px solid transparent;
		border-radius: @border-radius-base;

		// We remove the `outline` here, but later compensate by attaching `:hover`
		// styles to `:focus`.
		&:focus {
			outline: 0;
		}

		// Bars
		.icon-bar {
			display: block;
			width: 22px;
			height: 2px;
			border-radius: 1px;
			color: @cort-white;
			background-color: @cort-white;
		}
		.icon-bar + .icon-bar {
			margin-top: 4px;
		}

		@media (min-width: (@screen-md-min)) {
			display: none;
		}
	}

	@media (max-width: (@screen-md-min)) {
		background: @cort-red-dark;
		height: 50px;
		width: 100%; 
		background: @cort-red;
		border-bottom: 2px solid @cort-red-dark;
		.box-shadow(0px 2px 5px rgba(0,0,0,0.5));
		
		#nav-logo {
			position: absolute; 
			top: -3px;
			left: 40%;
		}
		
		ul {
			margin: 0;
			padding: 0;
			list-style: none;
			overflow: auto;
			
			& > li {
				float: left;
				position: relative;
				width: 100%;
				
				& > a {
					color: @cort-white;
					font-size: @font-size-base;
					line-height: 50px;
					padding: 0px;
					display: block;
					border-bottom: 1px solid rgba(0, 0, 0, .1);
					border-top: 1px solid rgba(255, 255, 255, .1);
					padding-left: 10px;
					
					&:hover {
						text-decoration: none;
						color: @cort-red;
					}
					
					&:hover + .dropdown {
						opacity: 1;
						visibility: visible;
					}
				}
			}
			
			.dropdown {
				& > ul {
					display: none;
				}
				
				& > ul.active {
					display: block;
					background-color: @cort-white;
					overflow: hidden;
					
					& > li {
						h2 {
							margin-left: 10px;
						}
					
						& > a {
							color: @cort-black;
							padding-left: 10px;

							&:hover {
								background-color: @cort-red;
								color: @cort-white;
							}
						}
					}
				}
			
				& > .dropdown-toggle {
					display: block;
					position: absolute;
					top: 0;
					right: 0;
					line-height: 51px;
					width: 20%;
					border-left: 1px solid rgba(255, 255, 255, 0.1);
					border-right: 1px solid rgba(255, 255, 255, 0.1);
					text-align: center;
					font-size: @font-size-large;
					color: @cort-white;
					cursor: hand;
					cursor: pointer;
					
					&:after {
						content: "+";
					}
					
					&.expanded:after {
						content: "\00d7";
					}
				}
			}
		}
		
		#nav-audience, #nav-quicklinks {
			padding-left: 10px;
			padding-right: 10px;
			overflow: auto;
		
			h3 {
				display: block;
				color: @cort-red;
			}
		}
		
		#nav-main {
			& > .container {
				display: block;
				content: "";
				width: 100%;
			}
		
			h3 {
				display: block;
				color: @cort-red;
				padding-top: 60px;
			}
		}
		
		#nav-quicklinks {
			& > .nav-search {
				position: absolute;
				top: 20px;
				width: 240px;
			
				& > a {
					display: none;
				}
				
				& > .search-box {
					input[type="text"], input[type="image"] {
						height: 30px;
						-webkit-transition: all 0.5s ease;
					  -moz-transition: all 0.5s ease;
					  -o-transition: all 0.5s ease;
					  -ms-transition: all 0.5s ease;
					  transition: all 0.5s ease;
					  width: 100%;
					}
				}
			}
		}
	}
	
	@media (max-width: @screen-sm-min) {
		#nav-logo {
			left: 30%;
		}
	}
	
	@media (min-width: @screen-lg-min) {
		.dropdown-double {
			width: 440px !important;
			
			&.dropdown-right {
				& > .dropdown-toggle {
					right: 7% !important;
				}
			}
			
			&.dropdown-left {
				& > .dropdown-toggle {
					left: 7% !important;
				}
			}
		}
		
		.dropdown-center {
			left: 0;
			margin-left: -110px;
			
			& > .dropdown-toggle {
				left: 32% !important;
			}
		}
		
		.dropdown-left {
			left: 0;
		}
		
		.dropdown-right {
			right: 0;
			
			& > .dropdown-toggle {
				right: 14% !important;
			}
		}
	}
	
	@media (min-width: @screen-md-min) {
		position: absolute; 
		top: 0; 
		height: 32px; 
		width: 100%;
		background: @cort-gray-charcoal;
		color: @cort-white;
		
		#nav-logo {
			display: none;
		}
		
		.dropdown-double {
			width: 440px !important;
			
			&.dropdown-right {
				& > .dropdown-toggle {
					right: 4%;
				}
			}
			
			&.dropdown-left {
				& > .dropdown-toggle {
					left: 4%;
				}
			}
		}
		
		.dropdown-center {
			left: 0;
			margin-left: -110px;
			
			& > .dropdown-toggle {
				left: 28%;
			}
		}
		
		.dropdown-left {
			left: 0;
		}
		
		.dropdown-right {
			right: 0;
			
			& > .dropdown-toggle {
				right: 6%;
			}
		}
		
		.dropdown {
			visibility: hidden;
			opacity: 0;
			position: absolute;
			top: 40px;
			width: 220px;
			padding-top: 20px;
			.transition(300ms ease);
			
			.box-shadow(0px 4px 4px rgba(0,0,0,0.5));
			z-index: 1000;
			background-image: url("../images/dropdown-menu-bg.png");
			background-repeat: no-repeat;
			background-color: @cort-gray-warm;
			
			&:hover {
				visibility: visible;
				opacity: 1;
			}
			
			& > ul {
				width: 220px;
				list-style: none;
				/*margin: 15px 0px 0px 0px;*/
				padding: 5px 0px 5px 0px;
				float: left;
				
				& > li {
					width: 100%;
					padding-top: 10px;
					padding-bottom: 10px;
					line-height: 20px;
					
					h2 {
						color: @cort-black;
						margin-left: 15px;
						margin-top: 0;
						margin-bottom: 0;
						font-size: 22px;
					}
					
					& > a {
						color: @cort-black;
						display: block;
						width: 220px;
						padding-left: 20px;
						
						&:hover {
							text-decoration: underline;
						}
					}
				}
			}
			
			& > .dropdown-toggle {
				display: block;
				position: absolute;
				top: 0;
				z-index: 1001;
				border-left: 50px solid transparent;
				border-right: 50px solid transparent;
				border-top: 15px solid @cort-red;
			}
		}
		
		#nav-main {
			position: absolute; 
			top: 115px; 
			width: 100%; 
			background: @cort-red;
			border-bottom: 2px solid @cort-red-dark;
			.box-shadow(0px 2px 20px rgba(0,0,0,0.5));
			left: 0; 
			float: left;
			
			h3 {
				display: none;
			}
			
			&.affix {
				position: fixed;
				top: 0;
				z-index: 1000;
				
				& > #nav-logo {
					opacity: 1;
				}
			}
			
			& > .container > ul {
				margin: 0;
				padding: 0;
				list-style: none;
				
				& > li {
					float: left;
					position: relative;
					width: 14.2%;
					
					& > a {
						color: @cort-white;
						font-size: 18px;
						line-height: 50px;
						padding: 0px;
						display: block;
						font-weight: normal;
						text-align: center;
						
						&:hover {
							text-decoration: none;
							color: @cort-black;
						}
						
						&:hover + .dropdown {
							opacity: 1;
							visibility: visible;
						}
					}
				}
			}
		}
		
		#nav-audience, #nav-quicklinks {
			& > ul {
				margin: 0;
				padding: 0;
				list-style: none;
				
				& > li {
					float: left;
					line-height: 32px;
					margin-right: 25px;
					
					& > a {
						color: @cort-white;
						font-size: 12px;
						
						&:hover {
							text-decoration: underline;
						}
					}
				}
			}
			
			h3 {
				display: none;
			}
		}
		
		#nav-audience {
			float: left;
		}
		
		#nav-quicklinks {
			float: right;
			
			& > ul {
				float: left;
			}
			
			& > .nav-search {
				float: right;
				position: relative;
				
				& > a {
					color: @cort-white;
					display: block;
					line-height: 32px;
				}
				
				& > .search-box {
					top: 32px;
					background-color: @cort-gray-charcoal;
					width: 235px;
					height: 0px;
					position: absolute;
					right: 0;
					-webkit-transition: all 0.5s ease;
					  -moz-transition: all 0.5s ease;
					  -o-transition: all 0.5s ease;
					  -ms-transition: all 0.5s ease;
					  transition: all 0.5s ease;
					
					input[type="text"], input[type="image"] {
						height: 0;
						padding: 0;
						-webkit-transition: all 0.5s ease;
					  -moz-transition: all 0.5s ease;
					  -o-transition: all 0.5s ease;
					  -ms-transition: all 0.5s ease;
					  transition: all 0.5s ease;
					  visibility: hidden;
					}
					
					&.active {
						height: 70px;
						padding: 10px;
						
						input[type="text"], input[type="image"] {
							padding: 6px 12px;
							height: 25px;
							visibility: visible;
						}
					}
				}
			}
		}
	}
	
	@media (min-width: @screen-lg-min) {
		#nav-audience, #nav-quicklinks {
			& > ul {
				& > li {
					margin-right: 40px;
				}
			}
		}
		
		#nav-quicklinks {
			& > .nav-search {
				& > .search-box {
					width: 325px;
				}
			}
		}
	}
}

#nav-secondary {
	.navbar-toggle {
		width: 40px;
		position: absolute;
		right: 25px;
		top: -25px;
		z-index: 1000;
		
		&.active {
			.navbar-toggle-inner {
				background: @cort-red;
				
				a {
					.icon-bar {
						color: @cort-white;
						background-color: @cort-white;
					}
				}
			}
		}
		
		.navbar-toggle-inner {
			width: 100%;
			height: 0;
			padding-bottom: 100%;
			border-radius: 50%;
			-webkit-box-shadow: 0 2px 20px -5px rgba(0,0,0,1);
			-moz-box-shadow: 0 2px 20px -5px rgba(0,0,0,1);
			box-shadow: 0 2px 20px -5px rgba(0,0,0,1);
			border: 2px solid @cort-gray-warm;
			overflow: hidden;
			background: @cort-gray;
			
			a {
				display: block;
				float: left;
				width: 100%;
				padding-top: 50%;
				padding-bottom: 50%;
				line-height: 1em;
				text-align: center;
				color: @cort-black;
				font-size: 1.2em;
				text-decoration: none;
				
				// We remove the `outline` here, but later compensate by attaching `:hover`
				// styles to `:focus`.
				&:focus {
					outline: 0;
				}

				// Bars
				.icon-bar {
					display: block;
					width: 22px;
					height: 2px;
					border-radius: 1px;
					color: @cort-gray-warmer;
					background-color: @cort-gray-warmer;
					margin-left: 7px;
					margin-top: -4px;
				}
				.icon-bar + .icon-bar {
					margin-top: 4px;
				}
			}
		}
		
		@media (min-width: (@screen-md-min)) {
			display: none;
		}
	}

	h3 {
		color: @cort-red;
		margin: 0;
		padding: 15px;
		background-color: @cort-white;
	}
	
	ul {
		margin: 0;
		padding: 0;
		list-style-type: none;
		background: @cort-gray-warm;
		
		& > li {
			position: relative;
			
			& > ul {
				display: none;
			}
				
			& > ul.active {
				display: block;
				overflow: hidden;
				
				& > li {
					& > a {
						padding-left: 45px;
					}
				}
			}
		
			&.active {
				& > a {
					color: @cort-red;
					font-weight: bold;
					background-color: @cort-white;
				}
			}
		
			& a {
				display: block;
				color: @cort-black;
				border-bottom: 1px solid rgba(0, 0, 0, 0.1);
				border-top: 1px solid rgba(255, 255, 255, 0.1);
				padding: 15px 15px 15px 15px;
				
				&:hover {
					background-color: @cort-gray;
					text-decoration: none;
				}
			}
			
			& > .dropdown-toggle {
				display: block;
				position: absolute;
				top: 0;
				right: 0;
				line-height: 53px;
				width: 20%;
				border-left: 1px solid rgba(0, 0, 0, 0.1);
				border-right: 1px solid rgba(0, 0, 0, 0.1);
				text-align: center;
				font-size: @font-size-large;
				color: @cort-black;
				cursor: hand;
				cursor: pointer;
				
				&:after {
					content: "+";
				}
				
				&.expanded:after {
					content: "\00d7";
				}
			}
		}
	}
	
	.nav-secondary-extra {
		padding: 15px;
		#gradient > .vertical (@cort-gray-warm, @cort-gray, 0%, 100%);
		
		h2 {
			margin-top: 0;
		}
	}
	@media (max-width: (@screen-lg-min)) {

	}
	
	@media (max-width: (@screen-md-min)) {
		width: 100%;
		
		ul {
			background: @cort-white;
		}
	
		.nav-secondary-extra {
			display: none;
		}
	
		.navbar-secondary-content {
			display: none;
			
			&.active {
				display: block;
				position: absolute;
				z-index: 999;
				left: 0;
				top: 0;
				width: 100%;
				-webkit-box-shadow: 0 2px 20px -5px rgba(0,0,0,1);
				-moz-box-shadow: 0 2px 20px -5px rgba(0,0,0,1);
				box-shadow: 0 2px 20px -5px rgba(0,0,0,1);
				border-bottom: 1px solid @cort-gray-warmer;
				
				h3 {
					background-color: @cort-red;
					color: @cort-white;
				}
				
				ul {

					& > li {
						&.active {
							& a {
								color: @cort-red;
							}
						}
						
						& > ul.active {
							display: block;
							overflow: hidden;
							
							& > li {
								& > a {
									color: @cort-black;
									padding-left: 45px;
								}
							}
						}
					
						& a {
							display: block;
							background-color: @cort-white;
							color: @cort-black;
							border-bottom: 1px solid rgba(0, 0, 0, 0.1);
							border-top: 1px solid rgba(255, 255, 255, 0.1);
							padding: 15px;
							
							&:hover {
								background-color: @cort-gray;
								text-decoration: none;
							}
						}
					}
				}
			}
		}
	}
	
	@media (max-width: (@screen-xs-min)) {

	}
}