//
// Carousel
// --------------------------------------------------

#cort-carousel-small {
	position: relative;
	overflow: hidden;
	width: 100%;
	margin-bottom: 20px;
	
	article.cort-slide {
		position: absolute;
		opacity: 0;
		-webkit-transition: opacity 0.5s ease-in-out;
		-moz-transition: opacity 0.5s ease-in-out;
		-ms-transition: opacity 0.5s ease-in-out;
		-o-transition: opacity 0.5s ease-in-out;
		transition: opacity 0.5s ease-in-out;
		z-index: 1;
		float: none;
		
		.cort-slide-overlay {
			display: none;
		}	
	
		&.active {
			float: left;
			position: relative;
			opacity: 1;
			-webkit-transition: opacity 0.5s ease-in-out;
			-moz-transition: opacity 0.5s ease-in-out;
			-ms-transition: opacity 0.5s ease-in-out;
			-o-transition: opacity 0.5s ease-in-out;
			transition: opacity 0.5s ease-in-out;
			z-index: 2;
			width: 100%;
			
			.cort-slide-overlay {
				display: block;
				position: relative;
				z-index: 3;
				background-color: @cort-white;
				overflow: hidden;
				padding: 0px 15px 15px 15px;
				
				.excerpt {
					padding: 0;
					margin: 0;
				}
				
				h3 {
					display: inline-block;
					width: auto;
					margin: 5px 0px 10px 0px;
				}
			}
		}
	}
	
	ul.cort-carousel-nav {
		display: block;
		position: absolute;
		bottom: 0;
		left: 45%;
		z-index: 5;
		
		@media (max-width: (@screen-lg-min)) {

		}
		
		@media (max-width: (@screen-md-min)) {

		}
		
		@media (max-width: (@screen-sm-min)) {

		}
		
		@media (max-width: (@screen-xs-min)) {

		}

		li {
			display: inline;
			
			button {
				font-size: 32px;
				line-height: 0;
				background: none;
				width: 20px;
				height: 20px;
				margin: 0;
				padding: 0;
				border: 0;
				cursor: pointer;
				color: @cort-white;
				opacity: 0.7;
				-webkit-transition-duration: 1s;
				-moz-transition-duration: 1s;
				-o-transition-duration: 1s;
				-ms-transition-duration: 1s;
				transition-duration: 1s;
				text-shadow: 0 0 2px #000;
				
				&.active {
					color: @cort-red;
				}
			}
		}
	}
}

#cort-carousel {
	position: relative;
	width: 100%;
	height: 500px;
	
	.prev-slide, .next-slide {
		opacity: 0.5;
		-webkit-transition: opacity 0.5s ease-in-out;
		-moz-transition: opacity 0.5s ease-in-out;
		-ms-transition: opacity 0.5s ease-in-out;
		-o-transition: opacity 0.5s ease-in-out;
		transition: opacity 0.5s ease-in-out;
		z-index: 99;
		position: absolute;
		width: 50px;
		height: 500px;
		cursor: hand;
		cursor: pointer;
		color: @cort-white;
		text-align: center;
		line-height: 500px;
		font-size: 50px;
		text-shadow: 1px 1px 2px rgba(0, 0, 0, 1);
	}

	.prev-slide {
		left: 0;
		background: url(data:image/svg+xml; base64,PD94bWwgdmVyc2lvbj0iMS4wIiA/Pgo8c3ZnIHhtbG5zPSJoIgaGVpZ2h0PSIxIiBmaWxsPSJ1cmwoI2dyYWQtdWNnZy1nZW5lcmF0ZWQpIiAvPgo8L3N2Zz4=);
		background: -moz-linear-gradient(left,rgba(11,11,11,0.5) 0%,rgba(11,11,11,0) 100%);
		background: -webkit-gradient(linear,left top,right top,color-stop(0%,rgba(11,11,11,0.5)),color-stop(100%,rgba(11,11,11,0)));
		background: -webkit-linear-gradient(left,rgba(11,11,11,0.5) 0%,rgba(11,11,11,0) 100%);
		background: -o-linear-gradient(left,rgba(11,11,11,0.5) 0%,rgba(11,11,11,0) 100%);
		background: -ms-linear-gradient(left,rgba(11,11,11,0.5) 0%,rgba(11,11,11,0) 100%);
		background: linear-gradient(to right,rgba(11,11,11,0.5) 0%,rgba(11,11,11,0) 100%);
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#80000000',endColorstr='#00000000',GradientType=1 );
	}

	.next-slide {
		right: 0;
		background: url(data:image/svg+xml; base64,PD94bWwgdmVyc2lvbj0iMS4wIiA/Pgo8c3ZnIHhtbG5zPSJoIgaGVpZ2h0PSIxIiBmaWxsPSJ1cmwoI2dyYWQtdWNnZy1nZW5lcmF0ZWQpIiAvPgo8L3N2Zz4=);
		background: -moz-linear-gradient(left,rgba(11,11,11,0) 0%,rgba(11,11,11,0.5) 100%);
		background: -webkit-gradient(linear,left top,right top,color-stop(0%,rgba(11,11,11,0)),color-stop(100%,rgba(11,11,11,0.5)));
		background: -webkit-linear-gradient(left,rgba(11,11,11,0) 0%,rgba(11,11,11,0.5) 100%);
		background: -o-linear-gradient(left,rgba(11,11,11,0) 0%,rgba(11,11,11,0.5) 100%);
		background: -ms-linear-gradient(left,rgba(11,11,11,0) 0%,rgba(11,11,11,0.5) 100%);
		background: linear-gradient(to right,rgba(11,11,11,0) 0%,rgba(11,11,11,0.5) 100%);
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000',endColorstr='#80000000',GradientType=1 );
	}
	
	.cort-carousel-wrap, .cort-carousel-slides {
		margin: 0 auto;
		width: 100%;
		max-width: 2000px;
		overflow: hidden;
		position: relative;
		height: 100%;
	}
	
	@media (max-width: (@screen-xs-min)) {
		.cort-carousel-wrap {
			position: absolute;
		}
	}
	
	article.cort-slide {
		width: 100%;
		height: 100%;
		position: absolute;
		
		&.active {
			z-index: 50;
			opacity: 1;
			
			.cort-slide-content {

				img {
					opacity: 1;
				}
				
				.left {
					left: 10%;
				}
				
				.right {
					right: 10%;
				}
				
				@media (max-width: (@screen-md-min)) {
					.left {
						left: 0;
					}
					
					.right {
						left: 0;
					}
				}
			}
		}

		.cort-slide-content {
			position: absolute;
			width: 100%;
			height: 100%;
			
			img {
				width: 2000px;
				position: absolute;
				right: 0;
				top: 0;
				height: 100%;
				opacity: 0;
				-webkit-transition: opacity 1s ease-in-out;
				-moz-transition: opacity 1s ease-in-out;
				-ms-transition: opacity 1s ease-in-out;
				-o-transition: opacity 1s ease-in-out;
				transition: opacity 1s ease-in-out;
			}
			
			.left {
				left: -1000px;
			}
			
			.right {
				right: -1000px;
			}
			
			@media (max-width: (@screen-lg-min)) {
			    top: 0;
				
				img {
					width: 1600px;
					right: 0;
				}
			}
			
			@media (max-width: (@screen-md-min)) {
				img {
					width: 1000px;
					right: 0;
				}
			}
			
			@media (max-width: (@screen-sm-min)) {
				bottom: 0;
			}
			
			@media (max-width: (@screen-xs-min)) {
				img {
					width: 180%;
					height: 90%;
					top: 0;
					right: 0;
				}
			}
			
			& > .cort-slide-overlay {
				position: absolute;
				bottom: 10%;
				width: 30%;
				-webkit-transition: all 1s ease-in-out;
				-moz-transition: all 1s ease-in-out;
				-ms-transition: all 1s ease-in-out;
				-o-transition: all 1s ease-in-out;
				transition: all 1s ease-in-out;
				z-index: 100;
				
				&:hover {
					
				}
				
				@media (max-width: (@screen-md-min)) {
					width: 100%;
					bottom: 0;
					left: -2000px;
				}
				
				a:hover {
					text-decoration: none;
				}
			
				h2 {
					display: inline-block;
					background-color: @cort-red;
					color: @cort-white;
					padding: 10px;
					margin-bottom: 2px;
					width: auto;
					
					@media (max-width: (@screen-md-min)) {
						margin-bottom: 0;
						background-color: @cort-gray-charcoal;
						width: 50%;
						display: block;
					}
					
					@media (max-width: (@screen-xs-min)) {
						margin-bottom: 0;
						background-color: @cort-gray-charcoal;
						width: 100%;
						display: block;
						font-size: @font-size-large;
					}
					
					&:hover {
						background-color: @cort-red-dark;
					}
				}
				
				p {
					padding: 10px 10px 25px 10px;
					background-color: @cort-white;
					color: @cort-black;
					
					& > .read-more-link {
						bottom: 10px;
						right: 5px;
					}
					
					@media (max-width: (@screen-md-min)) {
						display: none;
					}
				}
			}
		}
	}
	
	ul.cort-carousel-nav {
		display: block;
		margin: -40px auto 0 auto;
		position: absolute;
		bottom: 20px;
		left: 45%;
		z-index: 100;

		li {
			display: inline;
			
			button {
				font-size: 32px;
				line-height: 0;
				background: none;
				width: 20px;
				height: 20px;
				margin: 0;
				padding: 0;
				border: 0;
				cursor: pointer;
				color: @cort-white;
				opacity: 0.7;
				-webkit-transition-duration: 1s;
				-moz-transition-duration: 1s;
				-o-transition-duration: 1s;
				-ms-transition-duration: 1s;
				transition-duration: 1s;
				text-shadow: 0 0 2px #000;
				
				&.active {
					color: @cort-red;
				}
			}
		}
	}
	
	@media (max-width: (@screen-lg-min)) {
		height: 400px;
		
		.prev-slide, .next-slide {
			line-height: 400px;
			height: 400px;
		}
	}
	
	@media (max-width: (@screen-sm-min)) {

	}
	
	@media (max-width: (@screen-md-min)) {
		height: 250px;
		margin-top: 0;
		
		.prev-slide, .next-slide {
			line-height: 250px;
			height: 250px;
		}
	}
	
	@media (max-width: (@screen-xs-min)) {
		max-height: 450px;
		height: 100%;
		margin: 0;
		padding-bottom: 70%;
		
		.prev-slide, .next-slide {
			display: none;
		}
	}
}

#cort-carousel, #cort-carousel-small {
	&:hover {
		.prev-slide, .next-slide {
			opacity: 1;
		}
	}

	& > img {
		position: absolute; 
		width: 100%;
		top: 0;
		z-index: 100;
	}
}